@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

.rounded-btn {
  border-radius: 50px;
  border: solid #6d2e52 2px;
  width: 120px;
  text-align: center;
  padding: 10px;
  margin: 0 auto;
  color: black;
  text-decoration: none;
}

.rounded-btn:hover {
  border-radius: 50px;
  background: #6d2e52;
  width: 120px;
  text-align: center;
  padding: 10px;
  margin: 0 auto;
  color: #fff;
  text-decoration: none;
}

.flex {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

button {
  outline: none !important;
  border: none;
}

[aria-expanded='false'] .menu__icon--open {
  display: block;
  outline: none;
}

[aria-expanded='false'] .menu__icon--close {
  display: none;
  outline: none;
}

[aria-expanded='true'] .menu__icon--open {
  display: none;
  outline: none;
}

[aria-expanded='true'] .menu__icon--close {
  display: block;
  outline: none;
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .masonry {
    -webkit-column-count: 2;
            column-count: 2;
  }
}

@media (max-width: 1694px) {
  .welcome {
    background-position: center !important;
  }
}

@media (max-width: 991px) {
  nav {
    top: 57px;
    background: rgba(0, 0, 0, 0);
  }

  .changeColor {
    background: rgba(0, 0, 0, 0.7);
  }

  .changeColor .nav-link,
  .changeColor .fas {
    color: #fff;
  }

  .mobileNav {
    top: 0;
  }

  .welcome {
    background-position: center !important;
  }
}

/* Masonry on big screens */
@media only screen and (min-width: 1024px) {
  .desc {
    font-size: 1.25em;
  }

  .intro {
    letter-spacing: 1px;
  }

  .masonry {
    -webkit-column-count: 3;
            column-count: 3;
  }
}

@media only screen and (max-width: 900px) {
  .mobtime {
    display: block !important;
  }

  .top-bar {
    display: none !important;
  }

  .services {
    background-attachment: scroll !important;
  }

  .testmon .col,
  .services h2 {
    background: white;
    margin: 10px;
    box-shadow: 0px 0px 17px 0px rgba(0, 0, 0, 0.07);
    padding: 10px;
    text-align: center;
  }
}

@media (max-width: 899px) {
  nav {
    top: 0px !important;
    background: rgba(0, 0, 0, 0);
  }

  .services {
    background-attachment: scroll;
  }
}

@media (max-width: 769px) {
  #contact {
    height: 1000px !important;
  }

  #contact .row {
    grid-template-columns: 1fr !important;
  }

  #contact #glitter {
    background: url(/static/media/gold-glitter.92f624f3.png) center/cover no-repeat !important;
  }

  #contact .col1,
  #contact .col2 {
    height: 500px !important;
    width: 100%;
  }

  .staff .row .col-sm {
    margin-top: 1rem;
  }
}

@media only screen and (max-width: 769px) {
  .col2 h1 {
    margin-top: 4rem;
  }

  .services {
    background-attachment: scroll;
  }

  .welcome h1 {
    font-size: 1.5em !important;
  }

  .welcome .overlay .text-md-center {
    padding: 0 0.3rem !important;
  }

  .main img {
    max-width: 250px !important;
    max-height: 111px !important;
    margin-bottom: 1rem !important;
  }
}

@media (max-height: 376px) {
  .main img {
    max-width: 150px;
    max-height: 150px;
    margin-top: 4rem;
  }

  h1 {
    font-size: 1.5em;
    margin-top: 1.5rem;
  }

  .services {
    background-attachment: scroll;
  }
}

.top-bar {
  width: 100%;
  background: #6d2e52;
