/* The title */
.title {
  font-weight: 700;
}

/* The description */
.desc {
  font-size: 1em;
  font-style: italic;
  font-family: Georgia, serif;
}

/* Text-centering */
.title,
.desc,
.intro {
  text-align: center;
}

/* Masonry grid */
.masonry {
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  -webkit-column-fill: balance;
          column-fill: balance;
  -webkit-column-fill: initial;
          column-fill: initial;
}

/* Masonry item */
.masonry .brick {
  margin-bottom: 30px;
  display: inline-block;
  /* Fix the misalignment of items */
  vertical-align: top;
  /* Keep the item on the very top */
}

.masonry .brick:hover img {
  opacity: 0.75;
}

/* Bordered masonry */
.masonry.bordered {
  -webkit-column-rule: 1px solid #eee;
          column-rule: 1px solid #eee;
  grid-column-gap: 50px;
  -webkit-column-gap: 50px;
          column-gap: 50px;
}

.masonry.bordered .brick {
  padding-bottom: 25px;
  margin-bottom: 25px;
  border-bottom: 1px solid #eee;
}

/* Gutterless masonry */
.masonry.gutterless {
  grid-column-gap: 0;
  -webkit-column-gap: 0;
          column-gap: 0;
}

.masonry.gutterless .brick {
  margin-bottom: 0;
}

/* Masonry on tablets */
@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .masonry {
    -webkit-column-count: 2;
            column-count: 2;
  }
}

/* Masonry on big screens */
@media only screen and (min-width: 1024px) {
  .desc {
    font-size: 1.25em;
  }

  .intro {
    letter-spacing: 1px;
  }

  .masonry {
    -webkit-column-count: 3;
            column-count: 3;
  }
}

/**
  * BASIC TYPE
  */

img,
video {
  max-width: 100%;
  vertical-align: middle;
}

/**
  * CONTENT WRAPPER
  */

.wrapper {
  margin-left: auto;
  margin-right: auto;
  padding-right: 1.5em;
  padding-left: 1.5em;
  max-width: 1080px;
}

#contact {
  max-height: 1200px;
  height: 800px;
  width: 100%;
  overflow: hidden;
  font-family: 'Dancing Script', cursive;
  color: #6d2e52;
  border-bottom: 2px solid #6d2e52;
}

#contact .row {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

#contact #glitter {
  background: url(/static/media/gold-glitter.92f624f3.png);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.map {
  height: 100%;
  width: 100%;
  padding-left: 1rem;
}

.mobtime {
  display: none;
  font-size: 14px;
  background-color: #ffcc66;
  background-repeat: none;
  background-size: cover;
  background-position: center;
  margin: 0 1.5rem;
  margin-top: 4rem;
}

